      // 3.è°ƒç”¨æ•°ç»„çš„spliceæ–¹æ³•ï¼ŒæŠŠå›¾ç‰‡ä¿¡æ¯å¯¹è±¡ï¼Œä»picsæ•°ç»„ä¸­ç§»é™¤
      this.addForm.pics.splice(i, 1)
